import {request} from "../axios/request";

const baseUrl = '/api/admin'

const BlogInfoApi = {}

BlogInfoApi.loadInfo = () => {
  return request({
    url: `${baseUrl}/getBlogInfo`,
    method: 'get'
  })
}

BlogInfoApi.addVisit = () => {
  return request({
    url: `${baseUrl}/addVisit`,
    method: 'get'
  })
}

BlogInfoApi.updateInfo = info => {
  return request({
    url: `${baseUrl}/updateBlogInfo`,
    method: 'put',
    data: info
  })
}

BlogInfoApi.installInfo = info => {
  return request({
    url: `${baseUrl}/installInfo`,
    method: 'post',
    data: info
  })
}

export default BlogInfoApi
