import {request} from "../axios/request";

const baseUrl = '/api/admin'

const AdminApi = {}

AdminApi.getAdminInfo = () => {
  return request({
    url: `${baseUrl}/getAdminInfo`,
    method: 'get'
  })
}

AdminApi.uploadAvatar = avatar => {
  return request({
    url: `${baseUrl}/uploadAvatar`,
    header: {
      "Content-Type": "multipart/form-data"
    },
    method: 'post',
    data: avatar
  })
}

AdminApi.updateInfo = admin => {
  return request({
    url: `${baseUrl}/updateAdminInfo`,
    method: 'post',
    data: admin
  })
}

AdminApi.updatePwd = (oldPassword, password) => {
  return request({
    url: `${baseUrl}/updatePwd`,
    method: 'put',
    params: {
      oldPassword,
      password
    }
  })
}

AdminApi.login = (username, password) => {
  return request({
    url: `${baseUrl}/login`,
    method: 'get',
    params: {
      username,
      password
    }
  })
}

AdminApi.register = admin => {
  return request({
    url: `${baseUrl}/register`,
    method: 'post',
    data: admin
  })
}

AdminApi.isInstall = () => {
  return request({
    url: `${baseUrl}/isInstall`,
    method: 'get',
  })
}

AdminApi.deleteAllInfo = () => {
  return request({
    url: `${baseUrl}/deleteAdmin`,
    method: 'delete'
  })
}

AdminApi.logout = () => {
  return request({
    url: `${baseUrl}/logout`,
    method: 'get'
  })
}

export default AdminApi