<template>
  <div>
    <div v-if="show" class="loading">
      <a-spin :tip="tip">
        <a-icon slot="indicator" type="loading" style="font-size: 30px" spin />
      </a-spin>
    </div>
    <div v-else class="lwj-container" :style="{'animation-delay': '1s'}">
      <div class="zl-logo animated fadeInUp">
        <span>Hope</span>
      </div>
      <div class="animated">
        <div v-show="find_pwd">
          <a-input class="animated fadeInUp" v-model="username" placeholder="用户名/邮箱">
            <a-icon slot="prefix" type="user" style="color:rgba(0,0,0,.25)" />
          </a-input>
          <a-input-password v-model="password" class="mr-tt animated fadeInUp" placeholder="密码">
            <a-icon slot="prefix" type="lock" style="color:rgba(0,0,0,.25)" />
          </a-input-password>
          <a-button style="width: 100%" class="mr-tt animated fadeInUp" type="primary" @click="loginClick">登录</a-button>
          <a class="fl-r mr-tt animated fadeInUp" @click="find_pwd = !find_pwd">忘记密码?</a>
        </div>
        <div v-show="!find_pwd">
          <a-input class="animated fadeInUp" v-model="findPwd_form.email" placeholder="邮箱">
            <a-icon slot="prefix" type="mail" style="color:rgba(0,0,0,.25)" />
          </a-input>
          <a-input-group compact class="mr-tt animated fadeInUp">
            <a-input style="width: 60%"  placeholder="验证码">
              <a-icon slot="prefix" type="code" />
            </a-input>
            <a-button style="width: 40%" class="fl-r">
              获取验证码
            </a-button>
          </a-input-group>
          <a-input v-model="findPwd_form.new_password" class="mr-tt animated fadeInUp" placeholder="新密码">
            <a-icon slot="prefix" type="lock" style="color:rgba(0,0,0,.25)" />
          </a-input>
          <a-button class="mr-t animated fadeInUp" type="primary" @click="loginClick">确认修改</a-button>
          <a-icon class="mr-tt fl-r animated fadeInUp" style="color: #6e45e2" type="rollback" @click="find_pwd = !find_pwd"/>
        </div>
      </div>
    </div>
    <a-modal :closable="false" :width="355" :visible="codeVis" :footer="null" @cancel="handleCancel">
      <slide-verify
        :l="42"
        :r="10"
        :w="310"
        :h="155"
        ref="slideblock" @again="onAgain" @success="onSuccess" @fail="onFail" @refresh="onRefresh" :slider-text="text" :accuracy="accuracy"></slide-verify>
    </a-modal>
  </div>

</template>

<script>
  import AdminApi from "../../api/Admin";
  import BlogInfoApi from "../../api/BlogInfo";
  export default {
    name: "Login",
    data() {
      return {
        tip: 'Loading...',
        codeVis: false,
        msg: '',
        text: '向右滑动->',
        // 精确度小，可允许的误差范围小；为1时，则表示滑块要与凹槽完全重叠，才能验证成功。默认值为5
        accuracy: 3,
        show: true,
        username: '',
        password: '',
        find_pwd: true,
        findPwd_form: {
          email: '',
          code: '',
          new_password: ''
        }
      }
    },
    created () {
      window.document.title = '登录 - Hope'
      this.loading()
    },
    methods: {
      delayLogin() {
        this.tip = '登录成功! 请稍等一会'
        this.show = true
        setTimeout(() => {
          localStorage.setItem('token', this.username)
          this.$notification['success']({
            message: '欢迎',
            description: this.username + '，欢迎登录'
          })
          // this.$notification['info']({
          //   message: '通知',
          //   description: '请在退出时点击右上角删除按钮, 谢谢配合..',
          //   duration: null
          // })
          BlogInfoApi.addVisit().then(res => {
            console.log(res)
          }).catch(err => {
            this.$message.error(err.toString())
          })
          this.$router.push('/admin')
        }, 3000)
      },
      loading() {
        setTimeout(() => {
          this.verifyIsInstall()
        },3000)
      },
      async verifyIsInstall() {
        AdminApi.isInstall().then(res => {
          if (res.data === null) {
            this.show = false
            this.$router.push('/install')
          } else {
            this.show = false
          }
        })
      },
      loginClick() {
        this.codeVis = true
      },
      onSuccess(){
        this.codeVis = false
        if (this.username === '') {
          this.$message.warning('用户名不能为空')
          this.$refs.slideblock.reset();
          return
        }
        if (this.password === '') {
          this.$message.warning('密码不能为空')
          this.$refs.slideblock.reset();
          return
        }
        this.login()
      },
      onFail(){
      },
      onRefresh(){
      },
      onAgain() {
        // 刷新
        this.$refs.slideblock.reset();
      },
      handleCancel() {
        this.codeVis = false;
      },
      async login() {
        AdminApi.login(this.username, this.password).then(res => {
          if (res.code === 1000) {
            this.delayLogin()
          } else {
            this.$refs.slideblock.reset();
            this.$message.warning('用户名或密码不正确')
          }
        }).catch(err => {
          this.$message.error(err.toString())
        })
      }
    },
  }
</script>

<style lang="less" scoped>
  .lwj-container {
    background-color: #fff;
    position: absolute;
    border-radius: 5px;
    top: 45%;
    left: 50%;
    margin: -160px 0 0 -160px;
    width: 320px;
    padding: 18px 28px 28px 28px;
    -webkit-box-shadow: -4px 7px 46px 2px rgba(0,0,0,.1);
    box-shadow: -4px 7px 46px 2px rgba(0,0,0,.1);
  }
  .zl-logo {
    margin-bottom: 20px;
    text-align: center;
  }
  .zl-logo span {
    vertical-align: text-bottom;
    font-size: 38px;
    display: inline-block;
    font-weight: 600;
    color: #1790fe;
    background-image: linear-gradient(-20deg,#6e45e2,#88d3ce);
    -webkit-text-fill-color: transparent;
    -webkit-background-clip: text;
    background-clip: text;
  }
  .loading {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
  }
  .slide-verify-slider {
    margin-top: 0!important;
  }
  .bg {
    position: absolute;
    z-index: -99999;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    /*background-image: url('https://programmingwithlove.stackoverflow.wiki/loginBg?version=1588649922000');*/
    background-size: 100% 100%;
    background-position: top center;
  }
</style>